<template>
    <!-- 过渡动画 -->
    <transition name="fade">
        　　
       <div class="img-view" @click="bigImg">
        　　　　 <!-- 遮罩层 -->
        　　　　
        <div class="img-layer"></div>
        　　　　
        <div class="img">
       <img :src="imgSrc" />
            　　　　
        </div>
        　　</div>
        　　
    </transition>
</template>
<script>
    export default
    {
    props: ['imgSrc'],//接受图片地址
    methods: {
    bigImg()
        {
        // 发送事件
        this.$emit('clickit')

        }

    }

    }
</script>
<style scoped>

    /*动画*/

    .fade-enter-active, .fade-leave-active {
        transition : all .2 s linear;
         transform : translate3D(0, 0, 0);

    }

    .fade-enter,.fade-leave-active {
         transform : translate3D(100%, 0, 0);

    }

    /* bigimg */

    .img-view {
       position : inherit;
        width : 100%;
        height : 100%;

    }

    /*遮罩层样式*/

  .img-layer {
        position : fixed;
        z-index : 999;
         top : 0;
        left : 0;
        background : rgba(0, 0, 0, 0.7);
       width : 100%;
         height : 100%;
         overflow : hidden;

    }

    /*不限制图片大小，实现居中*/

    .img-view .img img {
      max-width : 50%;
        display : block;
      position : absolute;
       left : 0;
        right : 0;
        top:20%;
        margin : auto;
        z-index : 1000;

    }
</style>


