<template>

    <div id="app">
        <el-header class="app_header">
            <el-row>
                <el-col :xs="20" :sm="20" :md="20" :lg="20" :xl="20">
                    <div class="grid-content bg-purple-dark">
                        <el-dropdown   @command="handleCommand">
  <span class="el-dropdown-link">
   {{this.$store.state.username}}<i class="el-icon-arrow-down el-icon--right"></i>
  </span>
                            <el-dropdown-menu slot="dropdown" class="downinfolist">
                                <el-dropdown-item command="login" v-show="!this.$store.state.loginstate">登录</el-dropdown-item>
                                <el-dropdown-item command="register" v-show="!this.$store.state.loginstate">注册</el-dropdown-item>
                                <el-dropdown-item command="appindex" v-show="this.$store.state.loginstate">应用首页</el-dropdown-item>
                                <el-dropdown-item command="appuserhome" v-show="this.$store.state.loginstate">个人中心</el-dropdown-item>
                                <el-dropdown-item command="appmybuy" v-show="this.$store.state.loginstate">我的购买</el-dropdown-item>
                                <el-dropdown-item command="loginout" divided v-show="this.$store.state.loginstate">退出登录</el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                        <span class="app_header_title">应用商店</span>

                    </div>
                </el-col>
                <el-col :xs="4" :sm="4" :md="4" :lg="4" :xl="4" class="hidden-sm-and-down">
                    <div class="grid-content bg-purple-dark">
                        <img src="data:image/svg+xml;base64,">
                    </div>
                </el-col>
            </el-row>
        </el-header>
        <el-container :class="getcontailer">

            <el-tooltip class="item hidden-sm-and-down" effect="dark" content="上传自己的作品到应用商店" placement="top-start" >
                <el-button @click="uploadapp" v-popover:popover1 class="app_upload_btn" type="danger" icon="el-icon-upload" circle></el-button>
            </el-tooltip>
            <router-view/>
        </el-container>
        <login-form></login-form>

    </div>
</template>

<script>
    import LoginForm from '@/components/Login'

    export default
    {
        name: 'App',
                data()
        {
            return {
                mycontainer: 'container'
            }
        },
        components:{

            'login-form':LoginForm
        },
        created:function(){
            if(window.localStorage.getItem('accesstoken')){
                this.$store.state.accesstoken=window.localStorage.getItem('accesstoken')
                this.$store.state.username=window.localStorage.getItem('username')
                this.$store.state.loginstate=window.localStorage.getItem('loginstate')
                this.$store.state.uid=window.localStorage.getItem('uid')
                this.$store.state.loginstate =true
            }
        },
        computed
        :
            {
                getcontailer:function () {
                    var userAgentInfo = navigator.userAgent;

                    var Agents = new Array("Android", "iPhone", "SymbianOS", "Windows Phone", "iPad", "iPod");
                    var _class=this.mycontainer
                    for (var v = 0; v < Agents.length; v++) {
                        if (userAgentInfo.indexOf(Agents[v]) > 0) {
                            _class=''
                            break;
                        }


                    }
                    return _class
                }
            },
        methods:{
            handleCommand(command) {
                switch(command){
                    case 'loginout':
                        this.$store.commit('loginout')
                        break;
                    case 'login':
                        this.$store.state.showloginform = true
                        break;
                    case 'register':
                            window.location.href=this.$store.state.baseurl+'user/register.html'
                        break;
                    case 'appindex':
                        this.$router.push({name:'Index',params:{}})
                        break;
                    case 'appuserhome':
                        this.$router.push({name:'User',params:{uid:this.$store.state.uid},query:{uid:this.$store.state.uid}})
                        break;
                }

            },

            uploadapp:function(){
                if(this.$store.state.loginstate){
                    this.$router.push({name:'AddApplication',params:{}})
                }else{

                    this.$store.state.showloginform = true
                }

            }
        }
        }

</script>

<style>

</style>



