<template>
    <el-main class="app_border"  v-loading="loading">
        <el-row>
            <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="24">
                <el-carousel :interval="4000" type="card" height="200px" class="sp_imglist">
                    <el-carousel-item v-for="myimg in imgs" >
                        <img :src="myimg" @click="clickImg($event)"/>
                    </el-carousel-item>
                </el-carousel>
            </el-col>
            <el-col :xs="24" :sm="24" :md="14" :lg="14" :xl="24">
                <h1 class="app_title">{{item.sp_name}}</h1>
                <p>应用更新：最后更新于{{item.updatetime}}</p>
                <p>应用安装：{{item.sp_downs}}次</p>
                <p>应用价格：{{item.sp_price}}元</p>
                <el-button  @click="buy(item)" size="mini" type="danger" icon="el-icon-goods">购买</el-button>
            </el-col>

                <img src="../assets/userad.png" class="appinfouserad" />


        </el-row>


        <el-menu :default-active="activeIndex" class="el-menu-demo app_info_display" mode="horizontal"
        @select="handleSelect">
        <el-menu-item index="1">应用介绍</el-menu-item>
        <el-menu-item index="2" v-if="item.sp_updatelogtext!=null">更新日志</el-menu-item>
        <el-menu-item index="3" v-if="docfiles.length>0">说明文档</el-menu-item>
        <el-menu-item index="4">用户评论</el-menu-item>
        </el-menu>
        <div v-show="showjieshao">

            <div  ref="showappinfo">

            </div>
        </div>
        <div v-show="updatelog">

            <div  ref="showupdatelog">

            </div>
        </div>
        <div v-show="introfiles">

           <ul v-for="item in docfiles" class="app_doc_list">
               <li>
                   <span>{{item.substring(item.lastIndexOf('/')+9)}}</span>

                   <el-button @click="downfile(item)" type="primary" size="mini" class="downit">下载</el-button>
               </li>
           </ul>
        </div>
        <div v-show="showcomment">
            <p>应用综合评分：</p>
            <el-rate
                    v-model="item.sp_rate"
                    disabled
                    show-score
                    text-color="#ff9900"
                    score-template="{value}分">
            </el-rate>
        </div>
        <big-img v-if="showImg" @clickit="viewImg" :imgSrc="imgSrc"></big-img>

        <el-dialog
                v-if="this.$store.state.loginstate"
                title="购买信息"
                :visible.sync="centerDialogVisible"
                width="30%"
                center>
            <el-form  name="goumaifom" label-width="80px" size="mini" :action="this.$store.state.baseurl + 'appstore/aliapbuyytransfer'" method="post" target="_blank">

                <el-form-item label="应用名称">
                    <b>{{item.sp_name}}</b>
                </el-form-item>
                <el-form-item label="价格(元):">
                    <el-input  disabled v-model="item.sp_price" :value="item.sp_price"></el-input>
                </el-form-item>
                <el-form-item label="接收邮箱">
                    <el-input  type="text" name="receiveemail" v-model="receiveemail"></el-input>
                </el-form-item>
                <el-form-item label="购买备注">
                    <el-input type="textarea" name="beizhu" v-model="beizhu"></el-input>
                </el-form-item>
                <input type="hidden" name="spid" v-model="item.Id" />
                <input type="hidden" name="accesstoken" v-model="this.$store.state.accesstoken" />
                <input type="hidden" name="money" v-model="item.sp_price" />
                <button @click="checkinput" class="el-button el-button--primary el-button--small popchonzhibtn" type="button">
                提交
                </button>
            </el-form>


        </el-dialog>

        <el-dialog
                v-if="this.$store.state.loginstate"
                title="提示"
                :visible.sync="mycenterDialogVisible"
                width="30%"
                center>
            <span>如果确认购买成功，请点击确认！</span>
  <span slot="footer" class="dialog-footer">
    <el-button size="small"  @click="mycenterDialogVisible = false">购买失败</el-button>
      <el-button size="small"  type="primary" @click="mycenterDialogVisible = false">购买成功</el-button>
  </span>
        </el-dialog>
       </el-main>
</template>
        <script>
            import axios from 'axios'
            import BigImg from '@/components/BigImg'
            export default{
                name:'ShopInfo',
                        data(){
                    return {
                        receiveemail:'',
                        beizhu:'',
                        mycenterDialogVisible:false,
                        centerDialogVisible:false,
                        showcomment:false,
                        introfiles:false,
                        updatelog:false,
                        showjieshao:true,
                        showImg:false,
                        imgSrc: '',
                        activeIndex: "1",
                        loading:false,
                        imgs:[],
                        docfiles:[],
                        item:{},
                       form:{
                           id:0
                       }
                    }
                },
                components:{
                    'big-img': BigImg
                },
                methods:{
                    checkinput(){
                       if(this.receiveemail==''){
                           this.$message('接收邮箱不能为空，否则无法收到应用文件')
                           return false
                       }
                        var reg = new RegExp("^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$");
                        if(!reg.test(this.receiveemail)){ //正则验证不通过，格式不对
                            this.$message('邮箱格式错误')
return false;
}
                        this.mycenterDialogVisible=true
                        this.centerDialogVisible=false
                        document.goumaifom.submit();
                    },
                    buy(item){
                        if(!this.$store.state.loginstate){
                            this.$store.state.showloginform = true
                            return false
                        }
                        this.centerDialogVisible=true
                    },
                    downfile(item){
                        if(!this.$store.state.loginstate){
                            this.$store.state.showloginform = true
                        }else{
                            window.open(item)

                        }
                    },

                    handleSelect(key)
                    {

                        switch(key){
                            case '1':
                                this.showjieshao=true
                                this.updatelog=false
                                this.introfiles=false
                                this.showcomment=false
                                break;
                            case '2':
                                this.showjieshao=false
                                this.updatelog=true
                                this.introfiles=false
                                this.showcomment=false
                                break;
                            case '3':
                                this.showjieshao=false
                                this.updatelog=false
                                this.introfiles=true
                                this.showcomment=false
                                break;
                            case '4':
                                this.showjieshao=false
                                this.updatelog=false
                                this.introfiles=false
                                this.showcomment=true
                                break;
                        }

                    },
                clickImg(e) {
                   this.showImg = true;
// 获取当前图片地址
              this.imgSrc = e.currentTarget.src;
},
             viewImg(){
                 this.showImg = false;
        }
                },
                mounted:function(){

                         this.form.id=this.$route.query.id
                        this.loading = true
                        var _this = this;
                        // 设置一个开关来避免重负请求数据
                        var sw = true;



                            var params = 'ajax=' + encodeURIComponent(JSON.stringify(_this.form));
                            axios.post(this.$store.state.baseurl + 'appstore/getshopinfo', params).then(function (response) {
                                if(response.data==null){
                                    _this.$message('没有该应用信息,或者已经删除');
                                    setTimeout(function(){
                                        _this.$router.push({name:'Index',params:{}})
                                    },2000)
                                    _this.loading = false
                                    return false
                                }

                                _this.item=response.data
                                _this.$refs.showappinfo.innerHTML = _this.item.sp_info
                                _this.$refs.showupdatelog.innerHTML = _this.item.sp_updatelogtext
                                if(_this.item.app_imgs)
                                _this.imgs=_this.item.app_imgs.split(',')
                                if(_this.item.app_doc)
                                _this.docfiles=_this.item.app_doc.split(',')
                                _this.loading = false

                            }).catch(function (error) {
                                console.log(error);
                            });


                }
            }
        </script>
<style scoped>
    .el-carousel__container{
        height: 14rem;
    }
    .sp_imglist img{
        max-height: 14rem;
    }
    .el-carousel__item:nth-child(2n) {

    }

    .el-carousel__item:nth-child(2n+1) {

    }
</style>


